<template>
    <div id="login">
        <h3></h3>
        <el-input v-model="username" placeholder="" autocomplete="on"></el-input>
        <h3></h3>
        <el-input  v-model="password" placeholder="" @keyup.enter="onLogin" autocomplete="on" ></el-input>
        <el-button @click="onLogin" type="success" plain></el-button>
    </div>
</template>

<script>
    import {mapActions} from 'vuex'
    export default {
        data() {
            return {
                username: '',
                password:''

            }
        },
        methods:{
            ...mapActions(['login']),
            onLogin(){
                this.login({username:this.username,password:this.password})
                    .then(()=>{
                        this.$router.push({path:this.$route.query.redirect || '/'})
                    })
            }
        }
    }
</script>